import { useEffect, useRef, useState } from 'react';
import * as THREE from 'three';
import SceneInit from '../../lib/SceneInit';
import style from './figure-container.module.css';
import Pic from '../../image/oleg.png';

const FigureContainer = () => {

  const [pic, setPic] = useState(Pic);
  const picRef = useRef(null);

  useEffect(() => {

    const texture = new THREE.TextureLoader().load( pic );
    
    const test = new SceneInit('pic', 300, 300, 10);
    test.initialize();
    test.animate();
    const materials = [
      new THREE.MeshBasicMaterial( { color: 0xff0000 }),
      // правая сторона красная
      new THREE.MeshBasicMaterial( { color: 0x00ff00 }),
      // левая сторона зеленая
      new THREE.MeshBasicMaterial( { color: 0x0000ff }),
      //верх синий
      new THREE.MeshBasicMaterial( { color: 0xff00ff }),
      // низ пурпурный
      new THREE.MeshBasicMaterial( { color: 0xffff00 }),
      // лицевая сторона желтая
      new THREE.MeshBasicMaterial( { color: 0x00ffff })
      // задняя сторона цвета циан
      ];
    const geometry = new THREE.BoxGeometry( 5, 5, 5 );
    const material = new THREE.MeshBasicMaterial( { map: texture } );
    const cube = new THREE.Mesh( geometry, material );
    test.scene.add( cube );
   
    const test2 = new SceneInit('pic2', 300, 300, 10);
    test2.initialize();
    test2.animate();

    //const geometry2 = new THREE.BoxGeometry( 1, 1, 1 );
    const geometry2 = new THREE.CylinderGeometry( 2, 1, 4, 50 );
    const material2 = new THREE.MeshBasicMaterial( { map: texture } );
    const cube2 = new THREE.Mesh( geometry2, material2 );
    test2.scene.add( cube2 );
   
  }, [pic]);



  const addPicFileHandleer = (event) => {
    if(!event.target.files[0] && event.target.files.length === 0) {
      alert('inter file');
      return;
    } else {
      setPic(URL.createObjectURL(event.target.files[0]));
    }
  };

  return (
    <div>
      <canvas id='pic' className={style.pic}/>
      <canvas id='pic2' className={style.pic}/>
      <form>
        <fieldset>
          <legend>Add Pic</legend>
          <p>
            <input type='file' onChange={addPicFileHandleer}/>
            <label>file png</label>
          </p>
        </fieldset>
      </form>
      <img ref={picRef} alt={'picure'} src={pic} className={style['img-pic']} ></img>
     </div>
  );
}
export default FigureContainer;